
[v-cloak] {
  display: none;
}
body {
  color: #7a7a7a;
  font-size: 16px;
}
.title {
  margin-top: 50px;
  text-align: center;
  font-size: 2.5em;
}
@media screen and (min-width: 600px) {
body {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 95vh;
    width: 99%;
}
.title {
    margin: 0;
    margin-bottom: 97px;
}
}
.attention {
  color: #465C90;
}



